.profile {
    max-width: 880px;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

@media screen and (max-width:930px) {
    .profile {
        max-width: 581px;
        flex-direction: column;
        gap: 34px;
    }
}